import React from 'react'
import {
  BrowserRouter as Router,
  Route,
  NavLink } from 'react-router-dom'
import {
  TunesScreen,
  SetlistScreen,
  RandomScreen,
  MenuButton,
  MenuDropdown } from './components'
import {
  debounce,
  findIndex } from 'lodash'
import Hammer from 'hammerjs'
import { TweenLite } from 'gsap'
import tunesJson from './tunes'
import './App.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tunes: tunesJson,
      setlist: [],
      random: {},
      menuIsHidden: true,
      sortBy: 'title',
      filters: ['all'],
      activeRoute: '/tunes',
      tunesScrollPosition: 0,
      setlistScrollPosition: 0
    }
    this.setSortBy = this.setSortBy.bind(this)
    this.setAppState = this.setAppState.bind(this)
    this.toggleFilter = this.toggleFilter.bind(this)
    this.toggleSelected = this.toggleSelected.bind(this)
    this.getNewRandomTune = this.getNewRandomTune.bind(this)
  }

  componentDidMount() {
    // Get tunes from API
    // Uncomment in production
    // ----------------------------------------------------
    // fetch('https://thomas-mcevoy.com/api/tunes/')
    //   .then(response => JSON.parse(response))
    //   .then(responseJson => this.setState({
    //     tunes: responseJson
    //   }))
    //   .catch(error => console.log(error))

    this.getNewRandomTune()
    
    this.hammer = Hammer(this.refs.app, {touchAction: 'auto'})
    this.hammer.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL })
    this.hammer.on('swipeup', (e) => this.handleSwipeUp(e))

    window.addEventListener('scroll', debounce((e) => {
      if (window.location.pathname === '/tunes')
        this.setState({tunesScrollPosition: window.scrollY})
      else if (window.location.pathname === '/setlist')
        this.setState({setlistScrollPosition: window.scrollY})
    }, 100))
  }

  handleSwipeUp(e) {
    TweenLite.to(this.refs.dropdownMenu, .1, {y: 0})
    this.setState({menuIsHidden: true})
  }

  render() {
    return (
      <Router>
        <div className="App" ref="app">
          <header className="App-header">
            <div className="Router">
              <NavLink className="Router-link" to="/tunes">Tunes</NavLink>
              <NavLink className="Router-link" to="/setlist">Setlist</NavLink>
              <NavLink className="Router-link" to="/random">Random</NavLink>
            </div>
            <MenuButton
              setAppState={this.setAppState}
              menuIsHidden={this.state.menuIsHidden}
            />
          </header>
          <MenuDropdown
            ref="dropdownMenu"
            sortBy={this.state.sortBy}
            filters={this.state.filters}
            menuIsHidden={this.state.menuIsHidden}
            setSortBy={this.setSortBy}
            setAppState={this.setAppState}
            toggleFilter={this.toggleFilter}
          />
          <main className="App-main">
            <Route path="/tunes" render={() =>
              <TunesScreen
                tunes={this.state.tunes}
                filters={this.state.filters}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.tunesScrollPosition}
              />
            }/>
            <Route path="/setlist" render={() =>
              <SetlistScreen
                setlist={this.state.setlist}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.setlistScrollPosition}
              />
            }/>
            <Route path="/random" render={() => 
              <RandomScreen
                random={this.state.random}
                getNewRandomTune={this.getNewRandomTune}
              />
            }/>
          </main>
        </div>
      </Router>
    )
  }

  getNewRandomTune() {
    const { tunes } = this.state
    this.setState({random: tunes[Math.floor(Math.random() * tunes.length)]})
  }

  setAppState(newState) {
    this.setState(newState)
  }

  setSortBy(str) {
    this.setState((prevState, props) => {
      let tunes = [...prevState.tunes]
      return {
        sortBy: str,
        tunes: tunes.sort((a, b) => {
          if (str === 'year') {
            return a.year - b.year
          }
          else {
            const strA = a[str]
            const strB = b[str]
            if (strA < strB) return -1
            if (strA > strB) return 1
            return 0
          }
        })
      }
    })
  }

  toggleFilter(str) {
    const index = this.state.filters.indexOf(str)
    if (index === -1) {
      this.setState((prevState, props) => {
        return { filters: [...prevState.filters, str] }
      })
    }
    else {
      this.setState((prevState, props) => {
        return { filters: prevState.filters.filter((_, i) => i !== index) }
      })
    }
  }

  toggleSelected(tune) {
    this.setState((prevState, props) => {
      const getNextTunes = (tune, prevTunes) => {
        let nextTunes = [...prevTunes]
        const index = findIndex(prevTunes, {title: tune.title})
        let updatedTune = Object.assign({}, prevTunes[index])
        updatedTune.selected = updatedTune.hasOwnProperty('selected') ? 
          !updatedTune.selected : true
        nextTunes[index] = updatedTune
        return nextTunes
      }

      const getNextSetlist = (tune, prevSetlist) => {
        let nextSetlist = [...prevSetlist]
        const index = findIndex(prevSetlist, {title: tune.title})
        if (index === -1) nextSetlist.push(tune)
        else nextSetlist.splice(index, 1)
        return nextSetlist
      }

      return {
        tunes: getNextTunes(tune, prevState.tunes),
        setlist: getNextSetlist(tune, prevState.setlist)
      }
    })
  }
}

export default App



